#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: var(--bs-footer-bg);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 70px;
  padding: 0 calc(1.5rem / 2) 0 0;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.03);
}
.navbar-header .dropdown .show.header-item {
  background-color: var(--bs-tertiary-bg);
}

body[data-topbar=dark] .dropdown .show.header-item {
  background-color: rgba(255, 255, 255, 0.05);
}

.navbar-brand-box {
  padding: 0 1.5rem;
  width: 240px;
}

.logo {
  line-height: 70px;
}
.logo .logo-sm {
  display: none;
}

.logo-light {
  display: none;
}

.page-title {
  margin: 0;
  line-height: 70px;
  font-size: 20px;
}

/* Search */
.search-wrap {
  color: #1e2835;
  z-index: 9997;
  position: absolute;
  top: 0;
  display: flex;
  width: calc(100% - 240px);
  right: 0;
  height: 70px;
  padding: 0 15px;
  transform: translate3d(0, -100%, 0);
  transition: 0.3s;
}
.search-wrap form {
  display: flex;
  width: 100%;
}
.search-wrap .search-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.search-wrap .search-input {
  flex: 1 1;
  border: none;
  outline: none;
  box-shadow: none;
  background-color: transparent;
}
.search-wrap .close-search {
  width: 36px;
  height: 64px;
  line-height: 64px;
  text-align: center;
  color: inherit;
  font-size: 24px;
}
.search-wrap .close-search:hover {
  color: #f24734;
}

.search-wrap.open {
  transform: translate3d(0, 0, 0);
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: var(--bs-body-color);
}

@media (max-width: 992px) {
